.container {
    display: flex;
    flex-direction: row;
    height: 100vh;
    width: 100%;
  }
  
  .sidebar {
    width: 20%;
    height: 100%;
  }
  
  .main {
    background-color: #EDF1D6;
    flex-grow: 1;
    height: 100vh;
    width: 80%;
  }

  h1{
    text-align: center;
  }

  @media only screen and (max-width: 768px) {

   .main{
    height: 200vh;
    width: 2000vh;
   }

   h1{
    font-size: 0.5rem;
    text-align: center;
  }

    
  }
  
